.projects {
  margin: 0 5vw;
}
.heading {
  font-size: 4vw;
  text-align: center;
  color: rgb(40, 158, 255);
}
.mainCard {
  display: flex;
  box-shadow: 2px 2px 2px 2px rgb(2 119 189);
  border-radius: 10px;
  margin-bottom: 10vw;
}
.mainCard div {
  padding: 4vw;
}
.cardLeft img {
  width: 40vw;
  transition: transform 1s;
}
.cardLeft img:hover {
  transform: scale(1.05);
  box-shadow: 2px 2px 2px 2px rgb(2 119 189);
}
.cardRight p {
  font-size: 1vw;
}
.cardRight h1 {
  font-size: 2vw;
}
.proLinks a {
  color: white;
  margin: 1rem;
  text-decoration: none;
  font-size: 1.5vw;
  padding: 1rem;
}
.logo {
  size: 400px;
}
.proLinks a:hover {
  box-shadow: 2px 2px 2px 2px rgb(2 119 189);
}

@media screen and (max-width: 1100px) {
  .mainCard {
    flex-direction: column;
  }
  .cardLeft img {
    width: 80vw;
  }
  .cardRight p {
    font-size: 2vw;
  }
  .proLinks a {
    font-size: 3vw;
  }
}
@media screen and (max-width: 320px) {
  .cardRight p {
    font-size: vw;
  }
  .proLinks a {
    font-size: 3vw;
    margin: 1vw;
  }
}
