.gitCalMain {
  padding: 0 5vw;
  margin: auto;
}
.gitCalMain div {
  /* border: 1px solid white; */
  padding: 10px;
}
.stats {
  display: flex;
  justify-content: space-evenly;
}
.stats img {
  width: 40vw;
}

.react-activity-calendar__count {
  font-size: 100px;
}
.gitCal div {
  display: flex;
  font-size: 2vw;
}

.gitCalMain h1 {
  font-size: 5vw;
  color: rgb(40, 158, 255);
  text-align: center;
}
