.home {
  padding-top: 5%;
  display: flex;
  flex-direction: row;
  margin: 5% 0;
  padding: 5%;
}

.png {
  border-radius: 5%;
  width: 90%;
}

.home .details h1 {
  font-size: 4vw;
  color: white;
}

.home .details p {
  font-size: 2vw;
  font-weight: 200;
  text-transform: uppercase;
  color: white;
}

.details {
  /* height: 40vh; */
  width: 50%;
  /* border: 1px solid white; */
}

.gif {
  width: 50%;
}

/* .details::after {
  content: "|";
  animation: cursor 1.1s infinite step-start;
} */

@media screen and (max-width: 680px) {
  .home {
    flex-direction: column;
  }

  .details {
    width: 100%;
    padding: 0 5vw;
  }
  .gif {
    width: 100%;
    padding: auto;
    text-align: center;
  }
  .home .details h1 {
    font-size: 5vw;
  }
  .home {
    padding-top: 10%;
  }
  .home .details p {
    font-size: 5vw;
  }
}

@media screen and (max-width: 320px) {
  .home {
    padding-top: 15%;
  }
}
