.about {
  display: flex;
  padding: 0 5vw;
  gap: 5vw;
}
.right {
  margin: auto;
  text-align: center;
}
.mainAbout h1 {
  font-size: 4vw;
  text-align: center;
  color: rgb(40, 158, 255);
}
.right p {
  font-size: 1.5vw;
}

.leftProfile img {
  border-radius: 50%;
  box-shadow: rgb(2 119 189) 0px 4px 6px -1px, rgb(2 119 189) 0px 2px 4px -2px;
  width: 20vw;
}

/* div {
  border: 1px solid red;
} */

@media screen and (max-width: 680px) {
  .about {
    flex-direction: column;
    text-align: center;
  }

  .mainAbout h1 {
    font-size: 10vw;
  }

  .leftProfile img {
    width: 50vw;
  }
  .right p {
    font-size: 4vw;
  }
}

