.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  padding: 0 1%;
  height: 90px;
  z-index: 10;
}

.header-bg {
  background-color: rgba(0, 0, 0, 0.8);
  transition: 0.5s;
}

.nav-menu {
  display: flex;
}

.name {
  font-size: 3vw;
}

.nav-menu li {
  padding: 0 1rem;
  list-style: none;
}

.nav-menu li :hover {
  color: rgb(2 119 189);
}

.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 500;
  text-decoration: none;
  color: #fff;
}

.hamburger {
  display: none;
}
.header-bg {
  width: 100vw;
}

@media screen and (max-width: 1040px) {
  .nav-menu {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.3s;
  }

  .name {
    font-size: 4vw;
  }

  .nav-menu.active {
    left: 0;
    background: rgba(0, 0, 0, 0.9);
    /* top: 400%; */
  }

  .nav-menu li {
    padding: 1rem 0;
  }

  .nav-menu li a {
    font-size: 2rem;
  }

  .hamburger {
    display: initial;
  }
  .header-bg {
    width: 100vw;
  }
}

@media screen and (max-width: 480px) {
  .header-bg {
    width: 100vw;
  }

  .header {
    width: 100vw;
  }
}
